
.pagination{font-size:13px
}
.pagination.is-small{font-size:12px
}
.pagination.is-left{-ms-flex-pack:start;justify-content:flex-start
}
.pagination.is-right{-ms-flex-pack:end;justify-content:flex-end
}
.pagination ul{-ms-flex-positive:0;flex-grow:0
}
.pagination ul i{line-height:24px
}
.pagination ul .is-small i{line-height:17px
}
.pagination .btn-jumper{width:32px;cursor:pointer
}
.pagination .btn-jumper a{display:none
}
.pagination .btn-jumper:after{content:"\2022\2022\2022";display:block;letter-spacing:2px;color:#ccc;text-align:center;font-size:12px;font-family:Arial;margin:0 3px
}
.pagination .btn-jumper:hover a{display:block
}
.pagination .btn-jumper:hover:after{display:none
}
.pagination .input{display:inline-block;width:45px;vertical-align:baseline
}
.pagination input::-webkit-outer-spin-button,.pagination input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0
}



