
.tooltip{background:#383838;color:#fff;padding:8px 10px;font-size:12px;line-height:12px;white-space:nowrap;box-shadow:4px 4px 8px rgba(0,0,0,0.3)
}
.tooltip-arrow{width:0;height:0;border-style:solid;position:absolute;margin:5px
}
.tooltip,.tooltip-arrow{z-index:1000000;pointer-events:none
}
.tooltip[x-placement^="bottom"]{margin-top:8px
}
.tooltip[x-placement^="bottom"] .tooltip-arrow{border-width:0 5px 5px 5px;border-color:transparent transparent #383838 transparent;top:-5px;left:calc(50% - 5px);margin-top:0;margin-bottom:0
}
.tooltip[x-placement^="top"]{margin-bottom:8px
}
.tooltip[x-placement^="top"] .tooltip-arrow{border-width:5px 5px 0 5px;border-color:#383838 transparent transparent transparent;bottom:-5px;left:calc(50% - 5px);margin-top:0;margin-bottom:0
}
.tooltip[x-placement^="right"]{margin-left:8px
}
.tooltip[x-placement^="right"] .tooltip-arrow{border-width:5px 5px 5px 0;border-color:transparent #383838 transparent transparent;left:-5px;top:calc(50% - 5px);margin-left:0;margin-right:0
}
.tooltip[x-placement^="left"]{margin-right:8px
}
.tooltip[x-placement^="left"] .tooltip-arrow{border-width:5px 0 5px 5px;border-color:transparent transparent transparent #383838;right:-5px;top:calc(50% - 5px);margin-left:0;margin-right:0
}



