
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
.notifications{position:fixed;top:20px;right:0;z-index:1257;pointer-events:none
}
.notifications.top-center{right:0;left:0;margin:0 auto
}
.notifications.top-left{right:auto;left:0
}
.notifications.bottom-left{top:auto;bottom:20px;right:auto;left:0
}
.notifications.bottom-center{top:auto;bottom:20px;right:0;left:0;margin:0 auto
}
.notifications.bottom-right{top:auto;bottom:20px
}
@media screen and (min-width: 769px){
.notifications{max-width:320px
}
}
.notifications .notification{margin:10px
}
.notifications .notification.has-icon{padding-left:45px
}
.notifications .notification.is-default{box-shadow:0 1px 4px rgba(0,0,0,0.2);background-color:#fff
}
.notifications .notification .close{float:right;margin:-13px -14px 0 20px
}



