
.alert{position:relative;min-width:240px;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translate3d(0, 0, 0);pointer-events:all
}
.alert.has-icon{padding-left:45px
}
.alert .title{margin-bottom:5px;font-weight:500;font-size:16px
}
.alert .notification-content{margin-top:0px
}
.wrap-icon{position:absolute;left:10px;top:50%;margin-top:-11px;width:25px;height:25px
}
.wrap-icon i{font-size:21px
}



