
.modal{transition:all .3s ease
}
.modal.align-baseline{-ms-flex-align:baseline;align-items:baseline
}
.modal-background{transition:opacity .3s ease
}
.modal-card{box-shadow:0 1px 4px rgba(0,0,0,0.2);border-radius:5px
}
.modal-card-head,.modal-card-foot{background-color:#fff;padding:16px
}
.modal-card-head{border-bottom:1px solid rgba(120,130,140,0.13)
}
.modal-card-foot{border-top:1px solid rgba(120,130,140,0.13);-ms-flex-pack:end;justify-content:flex-end
}
.modal-card-title{font-size:16px;font-weight:500
}
.align-baseline .modal-card{margin-top:120px
}
.modal-enter{opacity:0
}
.modal-leave-active{opacity:0
}
.modal-enter .modal-card,.modal-leave-active .modal-card{transform:scale(1.1)
}
.close{font-size:21px;font-weight:700;line-height:1;color:#000;text-shadow:0 1px 0 #fff;cursor:pointer;filter:alpha(opacity=20);opacity:.2;font-family:Arial,sans-serif
}
.close:focus,.close:hover{filter:alpha(opacity=50);opacity:.5
}



