
.data-table td,.data-table th{word-wrap:break-word;word-break:break-all
}
.data-table thead tr{background-color:#f6f7f8
}
.data-table thead tr .icon{vertical-align:middle
}
.data-table thead tr .filter-key{cursor:pointer
}
.data-table thead tr .is-sorting{background-color:#fafafa
}
.data-table thead tr th{position:relative
}
.data-table .sortable{cursor:pointer
}
.data-table .sortable:hover{background-color:#fafafa
}
.data-table .sort-trigger{position:absolute;right:5px;top:50%;margin-top:-10px
}
.data-table .sort-trigger .fa{vertical-align:baseline
}
.data-table .sort-trigger.sort-asc{margin-top:-6px
}
.data-table .sort-trigger.sort-desc{margin-top:-13px
}
.data-table-header .table{margin-bottom:0
}
.data-table-header .table th{border-bottom:none
}
.table.is-bordered th{border-color:#e6e3e3
}
.table-toolbar.level{margin-bottom:10px
}



