
.timeline-item{position:relative;border-color:rgba(120,130,140,0.13);padding-bottom:1px
}
.timeline-item:before{content:'';position:absolute;border-color:inherit;border-width:4px;border-style:solid;border-radius:50%;width:15px;height:15px;margin-left:-8px;top:2px;left:0
}
.timeline-item .timeline-item-main{margin-left:24px;padding-bottom:16px
}
.timeline-item .timeline-icon{position:absolute;left:-10px;z-index:1;width:20px;height:20px;border-width:10px;background:#d2d6de;border-radius:50%;text-align:center
}
.timeline-item .timeline-icon i{margin-top:4px;font-size:12px
}
.timeline-item .timeline-item-date{font-size:.85em
}
.timeline-item.is-primary:before{border-color:#3c81df
}
.timeline-item.is-primary .timeline-icon{background:#3c81df;color:#fff
}
.timeline-item.is-info:before{border-color:#3c81df
}
.timeline-item.is-info .timeline-icon{background:#00c0ef;color:#fff
}
.timeline-item.is-success:before{border-color:#3c81df
}
.timeline-item.is-success .timeline-icon{background:#6cc788;color:#fff
}
.timeline-item.is-warning:before{border-color:#f39c12
}
.timeline-item.is-warning .timeline-icon{background:#f39c12;color:#fff
}
.timeline-item.is-danger:before{border-color:#f56954
}
.timeline-item.is-danger .timeline-icon{background:#f56954;color:#fff
}



