
@import url(~flatpickr/dist/flatpickr.min.css);
.datepicker .has-icon i{transition:all .3s
}
.datepicker .has-icon .fa-times{display:none;cursor:pointer;pointer-events:all
}
.datepicker .has-icon:hover .fa-times{display:inline-block
}
.datepicker .has-icon:hover .fa-calendar{display:none
}



