
.menu{background-color:#fff;box-shadow:0 0 1px rgba(0,0,0,0.15)
}
.menu.is-dark{background-color:#2e3e4e;color:rgba(255,255,255,0.87)
}
.menu.is-dark .menu-label,.menu.is-dark .menu-list a{color:rgba(255,255,255,0.9);opacity:1
}
.menu.is-dark .menu-list a.is-active{background-color:rgba(0,0,0,0.075)
}
.menu.is-dark .menu-list a:hover{background-color:rgba(0,0,0,0.075)
}
.menu.is-dark .menu-list.float{background-color:#2e3e4e;border:1px solid #2e3e4e
}
.menu-label:not(:first-child){margin-top:15px
}
.menu-label{padding:10px 7px;margin-bottom:0
}
.menu-list li{position:relative
}
.menu-list li ul{border-left:none;margin:0;padding-left:0
}
.menu-list>li li a{padding-left:50px
}
.menu-list>li li li a{padding-left:80px
}
.menu-list>li.is-active{background-color:rgba(0,0,0,0.065)
}
.menu-list.float{position:absolute;left:100%;top:0;min-width:180px;background-color:#fff;border:1px solid #ddd
}
.menu-list.float>li a{padding-left:16px
}
.menu-list .divider{height:1px;margin:9px 0;overflow:hidden;background-color:rgba(120,130,140,0.13)
}
.menu-list a{padding:5px 16px;line-height:25px;border-radius:0;color:#000;opacity:.75
}
.menu-list a i{width:15px;margin-right:15px;line-height:25px
}
.menu-list a .nav-right{float:right
}
.menu-list a .nav-right i{margin-right:0
}



