
.progress-wrap{position:relative
}
.progress-wrap.info-outside{margin-right:40px
}
.progress-wrap.info-outside .progress-info{position:absolute;right:-40px;color:#76838f
}
.progress{display:block;width:100%;height:1rem;margin-bottom:1rem;border-radius:.25rem;overflow:hidden;background-color:rgba(0,0,0,0.065)
}
.progress-bar{display:inline-block;float:left;height:100%;text-align:center;border-radius:.25rem;background-color:#0074d9;font-size:.8em;color:rgba(255,255,255,0.9);transition:width .6s ease
}
.progress-bar.is-info{background-color:#00c0ef
}
.progress-bar.is-danger{background-color:#f56954
}
.progress-bar.is-danger{background-color:#f56954
}
.progress-bar.is-warning{background-color:#f39c12
}
.progress-bar.is-success{background-color:#6cc788
}
.progress-bar.is-large{font-size:1em
}
.progress-bar-striped,.progress-striped .progress-bar{background-image:linear-gradient(45deg, rgba(255,255,255,0.15) 25%, transparent 25%, transparent 50%, rgba(255,255,255,0.15) 50%, rgba(255,255,255,0.15) 75%, transparent 75%, transparent);background-size:40px 40px
}
.progress-bar.animated,.progress.animated .progress-bar{animation:progress-bar-stripes 2s linear infinite
}
@keyframes progress-bar-stripes{
from{background-position:40px 0
}
to{background-position:0 0
}
}



