
.steps-wrap{margin-left:0;margin-right:0;display:block
}
.step-header{margin-left:0;margin-right:0;display:-ms-flexbox;display:flex
}
.step-header .step-item{position:relative;display:block;-ms-flex-preferred-size:0;flex-basis:0;-ms-flex-positive:1;flex-grow:1;-ms-flex-negative:1;flex-shrink:1
}
.step-header .step-item i{line-height:23px
}
.line .step-item:last-child{-ms-flex-positive:0;flex-grow:0
}
.step-content{position:relative;margin:25px 0 10px;padding:12px 20px;overflow:hidden;-ms-flex-direction:column;flex-direction:column;-ms-flex:1 1;flex:1 1
}
.step-left{position:absolute;top:50%;transform:translateY(-50%)
}
.step-icon{width:26px;height:26px;font-size:16px;line-height:26px;color:#fff;text-align:center;background:#d2d6de;border-radius:50%
}
.step-desc{padding-left:40px;margin-left:-5px;padding-right:5px
}
.step-desc .step-title{font-weight:bold
}
.pills .step-item{background-color:#f3f7f9;padding:10px
}
.pills .step-item.is-active{color:#fff;background-color:#3c81df
}
.pills .step-item.is-active .step-icon{color:#3c81df;background-color:#fff
}
.pills .step-item .step-description{width:80%;margin-left:35px
}
.line .step-item .step-desc{position:relative;display:inline-block;z-index:8;background-color:#fff
}
.line .step-item .step-left{z-index:9
}
.line .step-item .step-icon i{vertical-align:text-top
}
.line .step-item.is-active .step-icon{background-color:#3c81df
}
.line .step-item:before,.line .step-item:after{position:absolute;top:50%;width:100%;height:2px;content:"";background-color:rgba(0,0,0,0.065);transition:all .4s ease
}
.line .step-item:after{width:0;background-color:#62a8ea
}
.line .step-item.is-done:after{width:100%
}
.line .step-item.is-done .step-icon{background-color:#62a8ea
}
.line .step-item:before,.line .step-item:after{left:20px
}
.line .step-item:last-child:before,.line .step-item:last-child:after{width:0
}
.line .step-item .step-description{position:absolute;width:80%;left:35px
}



