
.blu-radio{padding-left:20px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.blu-radio input[type="radio"]{cursor:pointer;opacity:0;outline:none !important;position:absolute
}
.blu-radio:before{background-color:#ffffff;border-radius:100%;border:2px solid #3c81df;content:"";display:inline-block;width:16px;height:16px;left:0;position:absolute;transition:0.2s ease-in-out;-o-transition:0.2s ease-in-out;-webkit-transition:0.2s ease-in-out;outline:none !important
}
.blu-radio.on:after{content:'';position:absolute;width:0;height:0;top:4px;left:4px;border:4px solid;border-color:#3c81df;border-radius:100%;transition:0.2s ease-in-out;-o-transition:0.2s ease-in-out;-webkit-transition:0.2s ease-in-out
}
.blu-radio.is-primary.on:after{color:#fff
}



